import React  from "react";
import { useForm } from "react-hook-form";

const USERNAME_REGEX = /^[A-z][A-z0-9-_]{3,23}$/;
const PWD_REGEX = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%]).{8,24}$/;

function Login() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = React.useCallback((data) => {
    console.log(data);
  }, []);

  return (
    <section className={`login flexCenter`}>
      <div>
        <h1>Login</h1>
        <form onSubmit={handleSubmit(onSubmit)} className="flexCenter">
          <input
            name="pseudo"
            type="text"
            {...register("pseudo", {
              required: "Champ requis !",
              pattern: {
                value: USERNAME_REGEX,
                message: "Pseudo invalide !",
              },
            })}
            placeholder="Votre pseudo*"
          />
          {errors.pseudo && <p role="alert">{errors.pseudo.message}</p>}

          <input
            name="pwd"
            type="password"
            {...register("pwd", {
              required: "Champ requis !",
              pattern: {
                value: PWD_REGEX,
                message: "Mot de passe invalide !",
              },
            })}
            placeholder="Votre mot de passe*"
          />
          {errors.pwd && <p role="alert">{errors.pwd.message}</p>}

          <input value="Log In" type="submit" />
        </form>
      </div>
    </section>
  );
}

export default Login;
